<template>
  <div class="timeline">
    <h1 class="center">Âã§ÂãôË®òÈå≤</h1>
    <div class="center">
      <table>
        <thead>
          <tr>
            <th>ÈñãÂßãÊôÇÈñì</th>
            <th>ÁµÇ‰∫ÜÊôÇÈñì</th>
            <th>‰ΩúÊ•≠Âêç</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(timeLog, index) in timeLogs" :key="index">
            <td>{{ timeLog.startTime }}</td>
            <td>{{ timeLog.endTime }}</td>
            <td>{{ timeLog.projectName }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="center">
      <button @click="clear">„É≠„Ç∞„Çí„ÇØ„É™„Ç¢</button>
    </div>
  </div>
  
</template>

<script>
export default {
  name: 'TimeLine',
  data() {
    return {
      timeLogs: []
    }
  },
  created() {
    // „É≠„Éº„Ç´„É´„Çπ„Éà„É¨„Éº„Ç∏„Åã„Çâ„Éó„É≠„Ç∏„Çß„ÇØ„ÉàÊÉÖÂ†±„ÇíÂèñÂæó
    chrome.storage.local.get(['timeLogs'], (result) => {
      if (result.timeLogs) {
        this.timeLogs = Object.values(result.timeLogs)
      }
    });
  },
  watch: {
    timeLogs: {
      handler: function (timeLogs) {
        chrome.storage.local.set({ timeLogs: timeLogs });
      },
      deep: true
    }
  },
  methods: {
    clear() {
      if (confirm('Âã§ÂãôË®òÈå≤„ÅÆ„É≠„Ç∞„Çí„ÇØ„É™„Ç¢„Åó„Åæ„Åô„ÅãÔºü')) {
        this.timeLogs = []
      }
    }
  }
}
</script>

