<template>
  <div v-if="showPopup" class="popup-overlay" @click.self="showPopup = false">
    <div class="popup-content">
      <!-- „Åì„Åì„Å´„Éù„ÉÉ„Éó„Ç¢„ÉÉ„Éó„ÅßË°®Á§∫„Åó„Åü„ÅÑHTML„ÇíËøΩÂä† -->
      <h2>‰ΩúÊ•≠ÂàÜÈ°ûID</h2>
      <p>
        Redmine„ÅÆÊôÇÈñìÁôªÈå≤„ÅÆ„Éö„Éº„Ç∏„ÅßÈñãÁô∫ËÄÖ„ÉÑ„Éº„É´„Åã„ÇâÁôªÈå≤„Åó„Åü„ÅÑ‰ΩúÊ•≠ÂàÜÈ°û„ÅÆID„ÇíË™ø„Åπ„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ<br>
        ‰ª•‰∏ã„ÅÆÁîªÂÉè„ÅÆÂ†¥Âêà„ÅØ„ÄÅË®≠Ë®à‰ΩúÊ•≠:8„ÄÅÈñãÁô∫‰ΩúÊ•≠:9„Åß„Åô„ÄÇ
        <img src="/public/activity.png" alt="‰ΩúÊ•≠ÂàÜÈ°ûID„ÅÆË™ø„ÅπÊñπ" style="width: 100%;">
      </p>
      <button @click="showPopup = false">Èñâ„Åò„Çã</button>
    </div>
  </div>

  <h1 class="center">Âã§ÂãôË®òÈå≤„ÅÆÈõÜË®à</h1>
  <div class="center">
    <table>
      <thead>
        <tr>
          <th>‰ΩúÊ•≠Âêç</th>
          <th>ÂêàË®àÊôÇÈñì</th>
          <th>„ÉÅ„Ç±„ÉÉ„ÉàID</th>
          <th>‰ΩúÊ•≠ÂàÜÈ°ûID
            <button @click="showPopup = true">
              <i class="fa-solid fa-question"></i>
            </button>
          </th>
          <th>ÁôªÈå≤ÊôÇ„Ç≥„É°„É≥„Éà</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(project, index) in projects" :key="index"
          draggable="true"
          @dragstart="dragList($event,index)"
          @drop="dropList($event, index)"
          @dragover.prevent
          @dragenter.prevent
        >
          <!-- ‰ΩúÊ•≠Âêç -->
          <td>
            <div style="display: flex;">
              <a v-show="redmineUrl && project.redmineIssueId" :href="`${redmineUrl}/issues/${project.redmineIssueId}`" target="_blank">
                <img src="https://www.redmine.org/favicon.ico" alt="Redmine„ÅÆ„Ç¢„Ç§„Ç≥„É≥" style="width: 1rem;">
              </a>
              <input type="text" v-model="project.name">
            </div>
          </td>
          <!-- ÂêàË®àÊôÇÈñì -->
          <td><input type="time" class="time-input" :value="formatTimeForInput(project.sum)" @input="updateSum(project, $event.target.value)"></td>
          <!-- „ÉÅ„Ç±„ÉÉ„ÉàID -->
          <td>
            <input type="text" v-model="project.redmineIssueId" style="width: 4rem;">
          </td>
          <!-- ‰ΩúÊ•≠ÂàÜÈ°ûID -->
          <td>
            <input type="text" v-model="project.activityId" style="width: 4rem;">
          </td>
          <!-- ÁôªÈå≤ÊôÇ„Ç≥„É°„É≥„Éà -->
          <td>
            <input type="text" v-model="project.comment">
          </td>
        </tr>
      </tbody>
    </table>
    <button style="width: 9rem;" @click="registerAllToRedmine">Redmine„Å´‰∏ÄÊã¨ÁôªÈå≤</button>
  </div>

  <br>
  <div class="center">
    <button @click="apiKeyShow = !apiKeyShow">
      <span v-show="!apiKeyShow">Êé•Á∂öÊÉÖÂ†±ÁôªÈå≤</span>
      <span v-show="apiKeyShow">API„Ç≠„Éº„Å®Êé•Á∂öÂÖàURL„ÇíÈùûË°®Á§∫</span>
    </button>
    <div v-show="apiKeyShow">
      <input type="text" v-model="apiKey" placeholder="API„Ç≠„Éº„ÇíÂÖ•Âäõ" style="width: 300px;">
      <button @click="saveApiKey">‰øùÂ≠ò</button>
      <br>
      <input type="text" v-model="redmineUrl" placeholder="Redmine„ÅÆURL„ÇíÂÖ•Âäõ" style="width: 300px;">
      <button @click="saveRedmineUrl">‰øùÂ≠ò</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'SubmitTime',
  data() {
    return {
      projects: [],
      redmineUrl: '',
      apiKey: '',
      apiKeyShow: false,
      successProjects: [],
      failedProjects: [],
      deleteMode: false,
      showPopup: false
    }
  },
  created() {
    // „É≠„Éº„Ç´„É´„Çπ„Éà„É¨„Éº„Ç∏„Åã„Çâ„Éó„É≠„Ç∏„Çß„ÇØ„ÉàÊÉÖÂ†±„ÇíÂèñÂæó
    chrome.storage.local.get(['projects', 'apiKey', 'redmineUrl'], (result) => {
      if (result.projects) this.projects = Object.values(result.projects)
      if (result.apiKey) this.apiKey = result.apiKey
      if (result.redmineUrl) this.redmineUrl = result.redmineUrl
    });
  },
  watch:{
    // „Éó„É≠„Ç∏„Çß„ÇØ„ÉàÊÉÖÂ†±„ÅåÂ§âÊõ¥„Åï„Çå„Åü„Çâ„É≠„Éº„Ç´„É´„Çπ„Éà„É¨„Éº„Ç∏„Å´‰øùÂ≠ò
    projects: {
      handler: function (projects) {
        chrome.storage.local.set({ projects: projects })
      },
      deep: true
    }
  },
  computed: {
    // ÁôªÈå≤ÂÜÖÂÆπ„ÅÆÁ¢∫Ë™ç„ÅÆÁÇ∫„ÅÆ„É°„ÉÉ„Çª„Éº„Ç∏
    confirmMessage() {
      return this.projects.map(project => {
        console.log(project.sum)
        if (project.sum === '00:00:00') return
        if (!project.redmineIssueId) return
        if (!project.activityId) return
        return `${project.name} ${project.sum}ÊôÇÈñì`
      }).filter(Boolean).join('\n')
    }
  },
  methods: {
    registerAllToRedmine() {
      if (!confirm('‰ª•‰∏ã„ÅÆÂÜÖÂÆπ„ÅßRedmine„Å´‰∏ÄÊã¨ÁôªÈå≤„Åó„Åæ„Åô„ÅãÔºü' + '\n' + this.confirmMessage)) return
      // ÁôªÈå≤ÁµêÊûú„É°„ÉÉ„Çª„Éº„Ç∏„ÇíÂàùÊúüÂåñ
      this.successProjects = [];
      this.failedProjects = [];

      const requests = this.projects.map(project => {
        const totalHours = this.getTotalHours(project);
        if (totalHours === null) return Promise.resolve();

        // Redmine„Å´ÊôÇÈñì„ÇíÁôªÈå≤
        return axios.post(`${this.redmineUrl}/time_entries.json`, {
          time_entry: {
            issue_id: project.redmineIssueId,
            hours: totalHours, // Â§âÊèõ„Åó„ÅüÊµÆÂãïÂ∞èÊï∞ÁÇπÊï∞„Çí‰ΩøÁî®
            activity_id: project.activityId,
            comments: project.comment
          }
        }, {
          headers: {
            'Content-Type': 'application/json',
            'X-Redmine-API-Key': this.apiKey
          }
        })
        .then(response => {
          this.successProjects.push(`#${project.redmineIssueId} ${project.name}`);
          console.log('Success:', response.data.time_entry);
        })
        .catch(error => {
          this.failedProjects.push(`#${project.redmineIssueId} ${project.name}`);
          console.error('Error:', error);
        });
      });

      Promise.all(requests)
        .then(() => {
          let successMessage = 'üü¢‰ª•‰∏ã„ÅÆ„Éó„É≠„Ç∏„Çß„ÇØ„Éà„ÅÆÁôªÈå≤„Å´ÊàêÂäü„Åó„Åæ„Åó„Åü\n';
          successMessage += this.successProjects.join('\n');
          let failedMessage = 'üî¥‰ª•‰∏ã„ÅÆ„Éó„É≠„Ç∏„Çß„ÇØ„Éà„ÅÆÁôªÈå≤„Å´Â§±Êïó„Åó„Åæ„Åó„Åü\n';
          failedMessage += this.failedProjects.join('\n');
          alert(successMessage); // „Åô„Åπ„Å¶„ÅÆ„É™„ÇØ„Ç®„Çπ„Éà„ÅåÂÆå‰∫Ü„Åó„ÅüÂæå„Å´„Ç¢„É©„Éº„Éà„ÇíË°®Á§∫
          alert(failedMessage); // „Åô„Åπ„Å¶„ÅÆ„É™„ÇØ„Ç®„Çπ„Éà„ÅåÂÆå‰∫Ü„Åó„ÅüÂæå„Å´„Ç¢„É©„Éº„Éà„ÇíË°®Á§∫
        });
    },
    saveApiKey() {
      chrome.storage.local.set({ apiKey: this.apiKey })
      alert('API„Ç≠„Éº„Çí‰øùÂ≠ò„Åó„Åæ„Åó„Åü')
    },
    saveRedmineUrl() {
      chrome.storage.local.set({ redmineUrl: this.redmineUrl })
      alert('Redmine„ÅÆURL„Çí‰øùÂ≠ò„Åó„Åæ„Åó„Åü')
    },
    formatTimeForInput(timeString) {
      // "H:MM:SS"„Åæ„Åü„ÅØ"H:MM"ÂΩ¢Âºè„ÅÆÊôÇÈñì„Çí"HH:MM"„Å´Â§âÊèõ„Åô„Çã
      const parts = timeString.split(':')
      let hours = parts[0]
      let minutes = parts[1]

      // ÊôÇÈñì„Åå‰∏ÄÊ°Å„ÅÆÂ†¥Âêà„ÅØ„Çº„É≠„ÇíËøΩÂä†
      if (hours.length === 1) {
        hours = '0' + hours
      }
      // Áßí„ÇíÁÑ°Ë¶ñ„Åó„Å¶"HH:MM"ÂΩ¢Âºè„ÇíËøî„Åô
      return `${hours}:${minutes}`
    },
    updateSum(project, newValue) {
      // newValue„ÅØ"HH:MM"ÂΩ¢Âºè„ÅßÂèó„ÅëÂèñ„Çã
      // ÂøÖË¶Å„Å´Âøú„Åò„Å¶Áßí„ÇíËøΩÂä†„Åó„Å¶„Éó„É≠„Ç∏„Çß„ÇØ„Éà„ÅÆsum„Å´Ë®≠ÂÆö
      project.sum = newValue + ':00'
    },
    getTotalHours(project) {
      // project.sum„Åã„ÇâÊôÇÈñì„Å®ÂàÜ„ÇíÂàÜÈõ¢
      const [hours, minutes] = project.sum.split(':').map(Number);
      // ÂàÜ„ÇíÊôÇÈñì„Å´Â§âÊèõ„Åó„ÄÅÊôÇÈñì„Å´Âä†ÁÆó
      const totalHours = hours + minutes / 60;
      let message = '';
      if (totalHours === 0) message += 'ÂêàË®àÊôÇÈñì„Åå0 ';
      if (!project.redmineIssueId) message += '„ÉÅ„Ç±„ÉÉ„ÉàID„ÅåÊú™ÂÖ•Âäõ ';
      if (!project.activityId) message += '‰ΩúÊ•≠ÂàÜÈ°ûID„ÅåÊú™ÂÖ•Âäõ ';
      if (message) {
        this.failedProjects.push(`#${project.redmineIssueId} ${project.name}`+ 'Ôºö'+message)
        return null
      }
      return totalHours
    },
    dragList(event, dragIndex) {
      event.dataTransfer.effectAllowed = 'move'
      event.dataTransfer.dropEffect = 'move'
      event.dataTransfer.setData('drag-index',dragIndex)
    },
    dropList(event, dropIndex) {
      const dragIndex = event.dataTransfer.getData('drag-index')
      const deleteList = this.projects.splice(dragIndex,1)
      this.projects.splice(dropIndex, 0, deleteList[0])
    }
  }
}
</script>

